.syllabus {
    padding-top: .5rem;
	max-width: 40em;
}

.syllabus h1 {
	font-family: $sans-font;
	color: $theme-color;
	margin-top: 1.5rem;
    margin-bottom: 0rem;
}
.syllabus h2 {
	font-family: $sans-font;
	color: $theme-color;
    font-style: normal;
    font-weight:$normal-weight;
	line-height: 1;
	margin: 4rem 0 0 0;
}
.syllabus h3 {
	font-family: $serif-font;
    font-size: $larger-size;
	font-weight: $book-weight;
	font-style: normal;
	margin: 2rem 0 0 0;
}
.syllabus h4 {
    font-size: $large-size;
	font-weight: $normal-weight;
	font-style: italic;
	margin: 1rem 0 0 0;
}
.syllabus h5 {
    font-size: $smaller-size;
	font-style: italic;
	font-weight: $normal-weight;
	margin: 1rem 0 0 0;
}
.syllabus h6 {
    font-size: $smaller-size;
	font-family: $sans-font;
    font-weight: $normal-weight;
    font-style: normal;
    margin: .2rem 0 0rem 0;
}

.course-info {
	margin-bottom: 2rem;
}

.course-info p {
	font-family: $sans-font;
    font-size: $smaller-size;
    font-weight: $normal-weight;
    font-style: normal;
    margin: .2rem 0 0rem 0;
}

.course-info-link a {
	font-family: $mono-font;
}

.website {
	font-family: $mono-font;
	font-size: .9rem;
	font-weight: $normal-weight;
	font-style: normal;
}

